import React from "react";
import { Container, makeStyles } from "@material-ui/core";
import Page from "src/components/Page";
import Header from "./Header";
import JobCreateForm from "./JobCreateForm";
import { useHistory } from "react-router";
import { useSnackbar } from "notistack";
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: "100%",
    paddingTop: theme.spacing(3),
    paddingBottom: 100,
  },
}));

function JobCreateView() {
  const classes = useStyles();
  const history = useHistory();
  const { enqueueSnackbar } = useSnackbar();

  const handleSubmitSuccess = () => {
    enqueueSnackbar("Thêm mới tuyển dụng thành công", {
      variant: "success",
    });
    history.push("/app/management/jobs");
  };
  return (
    <Page className={classes.root} title="User Create">
      <Container maxWidth="lg">
        {/* <Header /> */}
        <JobCreateForm onSubmitSuccess={handleSubmitSuccess} />
      </Container>
    </Page>
  );
}

export default JobCreateView;
