import React, { useRef } from "react";
import { useDispatch } from "react-redux";
import clsx from "clsx";
import * as Yup from "yup";
import PropTypes from "prop-types";
import { Formik } from "formik";
import {
  Box,
  Button,
  Checkbox,
  FormHelperText,
  TextField,
  Typography,
  Link,
  makeStyles,
  InputAdornment,
  IconButton,
  Grid,
  ListItemText,
  Menu,
  MenuItem,
  FormControlLabel,
  Paper,
} from "@material-ui/core";
import { register } from "src/actions/accountActions";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import { useState } from "react";
import DraftEditor from "src/components/DraftEditor";
import { EditorState, convertToRaw } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { createJobRequest } from "../../../actions/jobActions";

const useStyles = makeStyles(() => ({
  root: {},
}));

function JobCreateForm({ className, onSubmitSuccess, ...rest }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const sortRef = useRef(null);
  const [editorState, setEditorState] = useState(() =>
    EditorState.createEmpty()
  );
  const handleEditorChange = (state) => {
    setEditorState(state);
    setBody(draftToHtml(convertToRaw(state.getCurrentContent())));
  };
  const [showPassword, setShowPassword] = useState(false);
  const [isOpen, setIsOpen] = useState(true);
  const [body, setBody] = useState("");

  const handleClickShowPassword = () => setShowPassword(!showPassword);
  const handleMouseDownPassword = () => setShowPassword(!showPassword);
  return (
    <Formik
      initialValues={{
        position: "",
        level: "",
        status: 1,
        body: "",
        count: 1,
        pathSEO: "",
      }}
      validationSchema={Yup.object().shape({
        position: Yup.string()
          .max(255)
          .required("Thông tin bắt buộc"),
        level: Yup.string()
          .max(255)
          .required("Thông tin bắt buộc"),
        count: Yup.number()
          .min(1)
          .max(100)
          .required("Thông tin bắt buộc"),
      })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          var objet = {
            position: values.position,
            level: values.level,
            status: isOpen ? 1 : 0,
            body: body,
            count: values.count,
            pathSEO: values.position
              .normalize("NFD")
              .replace(/[\u0300-\u036f]/g, "")
              .replaceAll(" ", "-"),
          };
          await dispatch(
            createJobRequest(
              objet,
              (res) => {
                console.log("createBlogRequest", res);
                setStatus({ success: true });
                setSubmitting(false);
                onSubmitSuccess();
              },
              (error) => {
                console.log("errorerror", error);
              }
            )
          );
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values,
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Grid container justify="space-between" spacing={3}>
            <Grid item xs={12} lg={6}>
              <TextField
                error={Boolean(touched.position && errors.position)}
                fullWidth
                helperText={touched.position && errors.position}
                label="Vị trí"
                margin="normal"
                name="position"
                onBlur={handleBlur}
                onChange={handleChange}
                type="position"
                value={values.position}
                variant="outlined"
              />

              <TextField
                error={Boolean(touched.count && errors.count)}
                fullWidth
                helperText={touched.count && errors.count}
                label="Số lượng"
                margin="normal"
                name="count"
                onBlur={handleBlur}
                onChange={handleChange}
                type="count"
                value={values.count}
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} lg={6}>
              <TextField
                error={Boolean(touched.level && errors.level)}
                fullWidth
                helperText={touched.level && errors.level}
                label="Trình độ"
                margin="normal"
                name="level"
                onBlur={handleBlur}
                onChange={handleChange}
                type="level"
                value={values.level}
                variant="outlined"
              />
              <FormControlLabel
                className={classes.stockField}
                control={
                  <Checkbox
                    checked={isOpen}
                    onChange={() => {
                      setIsOpen(!isOpen);
                    }}
                    name="inStock"
                  />
                }
                label="Đang mở"
              />
            </Grid>
          </Grid>
          <Box mt={3} mb={1}>
            <Typography variant="subtitle2" color="textSecondary">
              Nội dung
            </Typography>
          </Box>
          <Paper style={{ minHeight: 500, padding: 20 }} variant="outlined">
            <DraftEditor
              style={{ minHeight: 800 }}
              editorState={editorState}
              onEditorStateChange={handleEditorChange}
            />
          </Paper>
          <Box mt={2}>
            <Button
              color="secondary"
              disabled={isSubmitting}
              fullWidth
              size="large"
              type="submit"
              variant="contained"
            >
              Thêm mới
            </Button>
          </Box>
        </form>
      )}
    </Formik>
  );
}

JobCreateForm.propTypes = {
  className: PropTypes.string,
  onSubmitSuccess: PropTypes.func,
};

JobCreateForm.default = {
  onSubmitSuccess: () => {},
};

export default JobCreateForm;
