import axios from "src/utils/axios";
import imageService from "src/services/imageService";
export function uploadImageRequest(payload, onSuccess, onError) {
  return async (dispatch) => {
    try {
      const result = await imageService.uploadImage(payload);
      if (typeof onSuccess === "function") onSuccess(result);
      return result;
    } catch (error) {
      if (typeof onError === "function") onError(error);
      throw error;
    }
  };
}
export function getAllImageRequest(onSuccess, onError) {
  return async (dispatch) => {
    try {
      const list = await imageService.getAllImage();
      onSuccess(list.data);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}
